<script lang="ts">
  import { createEventDispatcher } from 'svelte';
  import { goto } from '$app/navigation';
  import { authService, isLoading, authError } from '$lib/stores/authStore';
  import { LogIn, Mail, Lock, AlertCircle, Eye, EyeOff } from 'lucide-svelte';
  import type { LoginRequest } from '$lib/types/auth';

  const dispatch = createEventDispatcher<{
    success: { user: any };
  }>();

  // Form state
  let formData: LoginRequest = {
    username: '',
    password: '',
    client_id: '',
  };

  let showPassword = false;
  let localError = '';
  let isSubmitting = false;

  // Form validation
  $: isFormValid = formData.username.trim().length >= 3 && formData.password.length >= 8;

  // Handle form submission
  async function handleSubmit() {
    if (!isFormValid) return;

    localError = '';
    isSubmitting = true;

    try {
      const result = await authService.login(formData);

      if (result.success) {
        // Login successful - redirect to dashboard
        dispatch('success', { user: result });
        goto('/dashboard');
      } else {
        localError = result.error || 'Login failed';
      }
    } catch (error) {
      localError = 'An unexpected error occurred';
      console.error('Login error:', error);
    } finally {
      isSubmitting = false;
    }
  }

  // Clear errors when user types
  function clearErrors() {
    localError = '';
  }

  // Toggle password visibility
  function togglePasswordVisibility() {
    showPassword = !showPassword;
  }
</script>

<div class="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8">
  <div class="max-w-md w-full space-y-8">
    <!-- Header -->
    <div class="text-center">
      <div class="mx-auto h-12 w-12 bg-blue-600 rounded-xl flex items-center justify-center">
        <Mail class="h-6 w-6 text-white" />
      </div>
      <h2 class="mt-6 text-3xl font-extrabold text-gray-900">
        Email Router Dashboard
      </h2>
      <p class="mt-2 text-sm text-gray-600">
        Sign in to manage your AI email automation
      </p>
    </div>

    <!-- Login Form -->
    <form class="mt-8 space-y-6 bg-white p-8 rounded-xl shadow-lg" on:submit|preventDefault={handleSubmit}>
      <div class="space-y-4">
        <!-- Username Field -->
        <div>
          <label for="username" class="block text-sm font-medium text-gray-700 mb-1">
            Username
          </label>
          <div class="relative">
            <input
              id="username"
              name="username"
              type="text"
              autocomplete="username"
              required
              bind:value={formData.username}
              on:input={clearErrors}
              class="appearance-none relative block w-full px-3 py-2 pl-10 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent focus:z-10 sm:text-sm"
              placeholder="Enter your username"
              disabled={isSubmitting || $isLoading}
            />
            <div class="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
              <Mail class="h-4 w-4 text-gray-400" />
            </div>
          </div>
        </div>

        <!-- Password Field -->
        <div>
          <label for="password" class="block text-sm font-medium text-gray-700 mb-1">
            Password
          </label>
          <div class="relative">
            {#if showPassword}
              <input
                id="password"
                name="password"
                type="text"
                autocomplete="current-password"
                required
                bind:value={formData.password}
                on:input={clearErrors}
                class="appearance-none relative block w-full px-3 py-2 pl-10 pr-10 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent focus:z-10 sm:text-sm"
                placeholder="Enter your password"
                disabled={isSubmitting || $isLoading}
              />
            {:else}
              <input
                id="password"
                name="password"
                type="password"
                autocomplete="current-password"
                required
                bind:value={formData.password}
                on:input={clearErrors}
                class="appearance-none relative block w-full px-3 py-2 pl-10 pr-10 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent focus:z-10 sm:text-sm"
                placeholder="Enter your password"
                disabled={isSubmitting || $isLoading}
              />
            {/if}
            <div class="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
              <Lock class="h-4 w-4 text-gray-400" />
            </div>
            <button
              type="button"
              class="absolute inset-y-0 right-0 pr-3 flex items-center"
              on:click={togglePasswordVisibility}
              disabled={isSubmitting || $isLoading}
            >
              {#if showPassword}
                <EyeOff class="h-4 w-4 text-gray-400 hover:text-gray-600" />
              {:else}
                <Eye class="h-4 w-4 text-gray-400 hover:text-gray-600" />
              {/if}
            </button>
          </div>
        </div>

        <!-- Client ID Field (Optional) -->
        <div>
          <label for="client_id" class="block text-sm font-medium text-gray-700 mb-1">
            Client ID 
            <span class="text-gray-400 text-xs">(optional)</span>
          </label>
          <input
            id="client_id"
            name="client_id"
            type="text"
            bind:value={formData.client_id}
            on:input={clearErrors}
            class="appearance-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent focus:z-10 sm:text-sm"
            placeholder="Leave blank for auto-detection"
            disabled={isSubmitting || $isLoading}
          />
        </div>
      </div>

      <!-- Error Display -->
      {#if localError || $authError}
        <div class="bg-red-50 border border-red-200 rounded-lg p-3">
          <div class="flex items-center">
            <AlertCircle class="h-4 w-4 text-red-600 mr-2" />
            <p class="text-sm text-red-700">
              {localError || $authError}
            </p>
          </div>
        </div>
      {/if}

      <!-- Submit Button -->
      <div>
        <button
          type="submit"
          disabled={!isFormValid || isSubmitting || $isLoading}
          class="group relative w-full flex justify-center py-3 px-4 border border-transparent text-sm font-medium rounded-lg text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed transition-colors duration-200"
        >
          {#if isSubmitting || $isLoading}
            <svg class="animate-spin -ml-1 mr-3 h-4 w-4 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
              <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
              <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
            </svg>
            Signing in...
          {:else}
            <LogIn class="h-4 w-4 mr-2" />
            Sign In
          {/if}
        </button>
      </div>

      <!-- Additional Links -->
      <div class="text-center">
        <p class="text-sm text-gray-600">
          Need help? Contact your system administrator
        </p>
      </div>
    </form>

    <!-- Footer -->
    <div class="text-center">
      <p class="text-xs text-gray-500">
        © 2024 Email Router. All rights reserved.
      </p>
    </div>
  </div>
</div>

<style>
  /* Custom focus styles for better accessibility */
  input:focus {
    box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
  }
  
  button:focus {
    box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
  }
</style> 